<template>
    <div>
        <v-dialog v-model="sellDialog" max-width="500px">
            <v-card>
                <v-card-title>
                    <span class="headline">Sell Wine</span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>
                                <p class="display-1 text--primary">{{ editedItem.wine.vintage }}  {{ editedItem.wine.producer }}</p>
                                <div class="text--primary">
                                    {{ editedItem.wine.regionName }}<br>
                                    {{ editedItem.units }}<br>
                                    €{{ editedItem.price }}
                                </div>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="blue darken-1" text @click="sellDialog = false">Close</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn text class="sellorder-btn" v-on:click="sellDialog = false">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-data-table :headers="headers" :items="wineList" item-key="id" hide-default-footer class="elevation-1">
            <template  v-slot:item="{ item }">
                <tr>
                    <td>{{ item.wine.vintage }}  {{ item.wine.producer }}</td>
                    <td class="text-xs-right">{{ item.wine.regionName }}</td>
                    <td class="text-xs-right">{{ item.size }}l</td>
                    <td class="text-xs-right">{{ item.units }}</td>
                    <td class="text-xs-right">€{{ item.price }}</td>
                    <td class="text-xs-right portfolio-profit" :class="item.profit > 0 ? 'positive' : 'negative'">€{{ item.profit }}</td>
                    <td class="justify-center layout px-0">
                        <v-btn text color="primary" class="portfolio-sell-btn" v-on:click="setSelected(item)">SELL</v-btn>
                    </td>
                </tr>
            </template>


            <template slot="no-data">
                <v-alert dense type="info" class="no-orders-info">There are no orders.</v-alert>
            </template>
        </v-data-table>
        <div class="portfolio-total-profit">Total Profit: <span :class="totalProfit > 0 ? 'positive' : 'negative'">€{{totalProfit}}</span></div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {GPortfolio} from '../store/getters';
    import {APortfolio, AWines} from '../store/actions';
    import { MWines } from "../store/mutations";

    export default {
        name: "Portfolio",
        data() {
            return {
                sellDialog: false,
                headers: [
                    { text: 'Wine', align: 'left', sortable: false, value: 'wine.producer' },
                    { text: 'Region', value: 'wine.regionName' },
                    { text: 'Size', value: 'size' },
                    { text: 'Units', value: 'units' },
                    { text: 'Price', value: 'price' },
                    { text: 'Profit', value: 'profit', sortable: false },
                    { text: '', sortable: false }
                ],
                editedItem: {
                    wine: {
                        vintage: '',
                        producer: '',
                        regionName: ''
                    },
                    size: '',
                    units: '',
                    price: '',
                    profit: ''
                }
            }
        },
        computed: {
            ...mapGetters({
                wineList: GPortfolio.GET_PORTFOLIO_WINE_LIST,
                totalProfit: GPortfolio.GET_PORTFOLIO_TOTAL_PROFIT,
            })
        },
        watch: {
            dialog(val) {
                val || this.close()
            }
        },
        methods: {
            ...mapActions({
                loadWines: APortfolio.LOAD_WINES
            }),
            setSelected (currWine) {
                this.editedItem = currWine;
                this.$store.dispatch(AWines.SELECT_WINE_ID, { wineId: currWine.wine.wineId, caseSize: currWine.caseSizeId });
                this.$store.dispatch(AWines.GET_WINE_DETAILS);
                this.$store.commit(MWines.SET_SELECTED_WINE_WAREHOUSE_NAME, currWine.warehouseName);
                this.sellDialog = true;
            }
        },
        created () {
            this.loadWines();
        }
    }
</script>

<style scoped>
    .portfolio-sell-btn {
        background: #024e7b !important;
        border-color: #024e7b !important;
        color: #ffffff !important;
        width: 80px;
        margin-top: 7px;
    }
    .portfolio-total-profit span {
        margin-left: 5px;
    }
    .portfolio-profit.negative,
    .portfolio-total-profit span.negative {
        color: red;
    }
    .portfolio-profit.positive,
    .portfolio-total-profit span.positive {
        color: green;
    }
    .portfolio-total-profit {
        display: flex;
        justify-content: flex-end;
        font-size: 16px;
        margin-top: 20px;
    }
    .sellorder-btn {
        background: #024E7B !important;
        border-color: #024E7B!important;
        color: #FFF !important;
        width: 180px;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
    }
    .v-card__title span {
        margin: 0 auto;
    }
    .no-orders-info {
        margin-top: 20px;
    }
</style>