<template>
    <div class="wine-market-wrapper">
        <p class="display-1 text--primary">Wine Market</p>

        <v-layout row wrap>
            <v-flex xs6>
                <v-select
                        :items="[{label:'All Wines', id: undefined, filter: ''},{ label: 'Most popular', id: undefined, filter: 'ShowPopular'}, { label: 'Last traded', id: undefined , filter: 'ShowOnlyLastTraded' }, { label: 'Last price change', id: undefined, filter: 'ShowLatestPrices'}, { label: 'Tightest spread', id: undefined,  filter: 'ShowOnlyTigtestSpread'}, { label: 'Bordeaux First Growth', id: 9,  filter: 'classificationsIds'}, { label: 'Burgundy Grand Cru', id: 2, filter: 'classificationsIds'}]"
                        v-model="selectedFilter"
                        single-line
                        item-text="label"
                        item-value="filter"
                        class="winemarket-wine-filter"
                        v-on:change="updateFilter"
                ></v-select>
            </v-flex>
            <v-flex xs6>
                <v-text-field
                        v-model="search"
                        append-icon="mdi-search"
                        label="Search"
                        single-line
                        hide-details
                ></v-text-field>
            </v-flex>
        </v-layout>

        <v-data-table :headers="headers" :items="wineItems" :search="search" class="elevation-1 winemarket-table">
            <template  v-slot:item="{ item }">
                <tr>
                    <td>
                        <v-img class="winemarket-wine-img" src="../assets/FR.png" /> {{ item.vintage }} {{ item.producerName }}
                    </td>
                    <td class="text-xs-right">
                        {{ item.regionName }}
                    </td>
                    <td class="text-xs-right">
                        {{ item.size }}l
                    </td>
                    <td class="text-xs-right">
                        €{{ item.sellPrice }}
                    </td>
                    <td class="text-xs-right">
                        €{{ item.buyPrice }}
                    </td>
                    <td class="text-xs-right">
                        €{{ item.caseSizeId }}
                    </td>
                    <td class="text-xs-right winemarket-change" :class="item.caseSizeId > 0 ? 'positive' : 'negative'">
                        {{ item.caseSizeId }}%
                    </td>
                </tr>
            </template>


            <template slot="no-data">
                <v-alert dense type="info" class="no-orders-info">There are no orders.</v-alert>
            </template>
        </v-data-table>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex';
    import { AMarket } from '../store/actions';
    import { GMarket, GCommon } from '../store/getters';
    import { MCommon } from '../store/mutations';

    export default {
        name: "Winemarket",
        data() {
            return {
                selected1: "Filter",
                modalVisible: false,
                perPage: 10,
                pageNumber: 1,
                favItems: [],
                search: '',
                headers: [
                    { text: 'Wine', value: 'producerName' },
                    { text: 'Region', value: 'regionName' },
                    { text: 'Size', value: 'size' },
                    { text: 'You Sell', value: 'units' },
                    { text: 'You Buy', value: 'price' },
                    { text: 'Last Trade', value: 'profit' },
                    { text: 'Change', value: 'profit' }
                ],
                selectedFilter: {
                    label:'All Wines',
                    id: undefined,
                    filter: ''
                },
                totalTableWines: 0,
                wineList: []
            }
        },
        computed: {
            ...mapGetters({
                wineItems: GMarket.GET_WINES,
                filterName: GMarket.GET_ACTIVE_FILTER,
                totalWines: GMarket.GET_TOTAL,
                shouldMarketUpdates: GCommon.GET_SHOULD_MARKET_UPDATES
            }),
            isWineListComplete() {
                return this.wineItems.length === this.totalWines
            }
        },
        methods: {
            setModalVisible: function(visible, string) {
                this.selected1 = string;
                this.modalVisible= visible;
                if( this.selected1 === 'Favourites') {
                    let _loadApp = async () => {
                        const favWines = await localStorage.getItem('favouriteWines');
                        let parsedFavWines = JSON.parse(favWines)
                        this.wineItems = parsedFavWines.filter(item => item.isInFavouriteList );
                    };
                    _loadApp();
                } else {
                    this.wineItems = [];
                    this.getWines()
                }
            },
            updateFilter() {
                let sortFilters = (this.selectedFilter.id === 2 || this.selectedFilter.id === 9) ? [this.selectedFilter.id] : [],
                    payload = {
                        wineIds: [],
                        caseSizeIds: [],
                        countryIds: [],
                        regionIds: [],
                        classificationsIds: sortFilters.classificationsIds,
                        producerIds: [],
                        colorIds: [],
                        vintages: [],
                        drinkFrom: null,
                        drinkTo: null,
                        showOnlyLastTraded: this.selectedFilter === 'ShowOnlyLastTraded',
                        showLatestPrices: this.selectedFilter === 'ShowLatestPrices',
                        showOnlyTigtestSpread: this.selectedFilter === 'ShowOnlyTigtestSpread',
                        showPopular: this.selectedFilter === 'ShowPopular',
                        from: 0,
                        to: 500
                    };

                console.log('_______CHANGING FILTERS MOTHER FUCKER_________');
                console.log(payload);

                this.removeSortFilter().then(() => {
                    if (this.selectedFilter.id === 2 || this.selectedFilter.id === 9) {
                        this.setActiveFilter({}).then(() => {
                            this.setSortFilter({filterId: this.selectedFilter.id, key: this.selectedFilter.filter}).then(() => {
                                this.$store.commit(MCommon.SET_SHOULD_MARKET_UPDATES, true);
                            })
                        });
                    }
                    else this.setActiveFilter({filter: this.selectedFilter.filter, value: this.selectedFilter.value ? this.selectedFilter.value : true, id: this.selectedFilter.id}).then(() => {
                        this.$store.commit(MCommon.SET_SHOULD_MARKET_UPDATES, true);
                    });
                })
            },
            ...mapActions({
                getWines: AMarket.GET_WINES,
                removeWines: AMarket.REMOVE_WINES,
                toggleSearch: AMarket.TOGGLE_SEARCH,
                setActiveFilter: AMarket.SET_ACTIVE_FILTER,
                setSortFilter: AMarket.SET_PICKED_SORT_FILTERS,
                removeSortFilter: AMarket.REMOVE_PICKED_SORT_FILTERS,
            })
        },
        created (){
            let setFavItem = async () => {
                let checkIfExists = await localStorage.getItem('favouriteWines');

                if (checkIfExists == null){
                    localStorage.setItem('favouriteWines', JSON.stringify(this.$store.state.wineMarketItems))
                };
            };

            setFavItem();

            if (this.shouldMarketUpdates) {
                this.removeWines(AMarket.REMOVE_WINES)
                this.getWines()
            }
        },
        watch: {
            wineItems: {
                immediate: true,
                deep: true,
                handler(v) {
                    this.totalTableWines = v.price;
                    this.wineList = v.units;
                }
            }
        }
    }
</script>

<style scoped>
    .winemarket-wine-filter {
        width: 320px;
        margin-left: 15px;
    }
    .wine-market-wrapper {
        padding: 15px;
    }
    .winemarket-change.negative {
        color: red;
    }
    .winemarket-change.positive {
        color: green;
    }
    .winemarket-wine-img {
        width: 22px;
        height: 15px;
        display: inline-block;
        margin-right: 5px;
        position: relative;
        top: 2px;
    }
</style>