<template>
    <div>
        <v-dialog v-model="modifyOrderDialog" max-width="500px">
            <v-card>
                <v-card-title>
                    <span class="headline">Edit Offer</span>
                </v-card-title>
                <v-card-text class="editoffer-container">
                    <v-container grid-list-md>
                        <v-layout wrap v-if="!editOrderFail.visible && !editOrderSuccess.visible">
                            <v-flex xs12>
                                <span class="display-1 text--primary card_label_name">{{selectedOrder.producer}}</span>
                                <div class="card_item_second">
                                    <v-img class="order-country-wine" src="../assets/france.png" />
                                    <span class="card_label_regular vintage-label">{{selectedOrder.vintage}}</span>
                                    <v-label class="card_label_regular region-label">{{selectedOrder.region}}</v-label>
                                </div>
                                <div class="card_item_third">
                                    <v-label class="card_label_regular blue_unit">{{selectedOrder.units}} {{selectedOrder.units == 1 ? 'UNIT' : 'UNITS'}}</v-label>
                                    <v-label class="card_label_regular blue_unit"> / €{{ selectedOrder.price }}</v-label>
                                    <v-label class="card_label_regular blue_unit"> x unit</v-label>
                                </div>
                            </v-flex>
                            <v-flex xs12>
                                <v-card class="mx-auto">
                                    <v-card-text>
                                        <v-layout class="text--primary">
                                            <v-flex xs6>
                                                <integer-plusminus :min="1"
                                                                   :max="maxUnits"
                                                                   :step="1"
                                                                   v-model="units">
                                                    <p>UNITS</p>
                                                    {{ units }}
                                                </integer-plusminus>
                                            </v-flex>
                                            <v-flex xs6 class="editorder-price-container">
                                                <integer-plusminus :min="1"
                                                                   :max="1000000"
                                                                   :step="10"
                                                                   v-model="price">
                                                    <p>PRICE</p>
                                                    {{ price }}
                                                </integer-plusminus>
                                            </v-flex>
                                        </v-layout>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-flex class="editorder-total-investment" xs12>
                                            TOTAL INVESTMENTS €{{selectedOrder.price * selectedOrder.units}}
                                        </v-flex>
                                    </v-card-actions>
                                </v-card>
                            </v-flex>
                        </v-layout>

                        <v-card-text v-if="editOrderFail.visible" class="addaddress-result-container">
                            <v-flex class="deladdress-result-img">
                                <v-img src="../assets/unsuccessful.gif"></v-img>
                            </v-flex>
                            <v-text class="cancel-order-fail-text">{{editOrderFail.text}}</v-text>
                        </v-card-text>

                        <v-card-text v-if="editOrderSuccess.visible" class="addaddress-result-container">
                            <v-flex class="deladdress-result-img">
                                <v-img src="../assets/successful.gif"></v-img>
                            </v-flex>
                            <v-text class="cancel-order-success-text">{{editOrderSuccess.text}}</v-text>
                        </v-card-text>

                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="blue darken-1" text  v-on:click="modifyOrderDialog = false">Close</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn text class="sellorder-btn" v-if="!editOrderFail.visible && !editOrderSuccess.visible" v-on:click="editOrder">Confirm Changes</v-btn>
                    <v-btn text class="sellorder-btn" v-if="editOrderFail.visible" v-on:click="editOrder">Try Again</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="cancelOrderDialog" max-width="320px">
            <v-card>
                <v-card-title>Cancel Order</v-card-title>

                <v-card-text class="cancel-order-container" v-if="!cancelOrderFail.visible && !cancelOrderSuccess.visible">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            Are you sure you want to cancel this order?
                        </v-layout>
                    </v-container>
                </v-card-text>

                <v-card-text v-if="cancelOrderFail.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/unsuccessful.gif"></v-img>
                    </v-flex>
                    <v-text class="cancel-order-fail-text">{{cancelOrderFail.text}}</v-text>
                </v-card-text>

                <v-card-text v-if="cancelOrderSuccess.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/successful.gif"></v-img>
                    </v-flex>
                    <v-text class="cancel-order-success-text">{{cancelOrderSuccess.text}}</v-text>
                </v-card-text>

                <v-card-actions>
                    <v-btn color="blue darken-1" class="close-change-pass-modal" text v-on:click="cancelOrderDialog = false">Close</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn class="cancel-order-btn" text v-if="!cancelOrderFail.visible && !cancelOrderSuccess.visible" v-on:click="cancelOrder">Delete</v-btn>
                    <v-btn class="cancel-order-btn" text v-if="cancelOrderFail.visible" v-on:click="cancelOrder">Try Again</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-data-table :headers="headers" :items="orders" item-key="id" hide-default-footer class="elevation-1">
            <template  v-slot:item="{ item }">
                <tr>
                    <td>{{ item.vintage }}  {{ item.producer }}</td>
                    <td class="text-xs-right">{{ item.region }}</td>
                    <td class="text-xs-right">{{ item.size }}l</td>
                    <td class="text-xs-right">{{ item.orderType }}</td>
                    <td class="text-xs-right">{{ item.units }}</td>
                    <td class="text-xs-right">€{{ item.price }}</td>
                    <td class="text-xs-right">{{ orderStatuses[item.orderStatus] }}</td>
                    <td class="justify-center layout px-0">
                        <v-btn text color="primary" class="order-modify-btn" v-on:click="openOrder(item);">
                            <v-icon left>mdi-pencil</v-icon>
                        </v-btn>
                        <v-btn text color="primary" class="order-cancel-btn" v-on:click="openCancelDialog(item.id)">
                            <v-icon left>mdi-close-circle</v-icon>
                        </v-btn>
                    </td>
                </tr>
            </template>

            <template slot="no-data">
                <v-alert dense type="info" class="no-orders-info">There are no orders.</v-alert>
            </template>
        </v-data-table>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from "vuex";
    import { AOrders, AWines, APortfolio } from "../store/actions";
    import { GOrders, GWines, GPortfolio } from "../store/getters";
    import { IntegerPlusminus } from 'vue-integer-plusminus';

    export default {
        name: "Orders",
        components: { IntegerPlusminus },
        data () {
            return {
                modifyOrderDialog: false,
                cancelOrderDialog: false,
                cancelOrderID: null,
                cancelOrderSuccess: {
                    visible: false,
                    text: 'Order was cancelled successfully!'
                },
                cancelOrderFail: {
                    visible: false,
                    text: 'Order cancellation failed!'
                },
                editOrderSuccess: {
                    visible: false,
                    text: 'Order was saved successfully!'
                },
                editOrderFail: {
                    visible: false,
                    text: 'Order update failed!'
                },
                headers: [
                    { text: 'Wine', align: 'left', sortable: false, value: 'producer' },
                    { text: 'Region', value: 'region' },
                    { text: 'Size', value: 'size' },
                    { text: 'Type', value: 'type' },
                    { text: 'Units', value: 'units' },
                    { text: 'Price', value: 'price' },
                    { text: 'Status', value: 'status', sortable: false },
                    { text: '', sortable: false }
                ],
                orderStatuses: {
                    1: 'Pending',
                    2: 'Filled',
                    3: 'Edited',
                    4: 'PartiallyFilled',
                    5: 'Cancelled'
                },
                units: 0,
                price: 0
            }
        },
        computed: {
            maxUnits() {
                let maximumUnits = 1000;

                if(this.selectedOrder.orderType === 'OFFER') {
                    let wine = this.portfolio.find(item => this.selectedOrder.wineId == item.wine.wineId);

                    if (wine) {
                        let wineUnits = wine.units;

                        for (let j=0; j<this.orders.length; j++) {
                            if (this.selectedOrder.wineId == this.orders[j].wineId && this.selectedOrder.id != this.orders[j].id) {
                                wineUnits -= this.orders[j].units;
                            };
                        };

                        maximumUnits = wineUnits;
                    }
                }

                return maximumUnits;
            },
            ...mapGetters({
                orders: GOrders.GET_BIDS_AND_OFFERS,
                portfolio: GPortfolio.GET_PORTFOLIO_WINE_LIST,
                selectedOrder: GOrders.GET_ACTIVE_ORDER,
                wineDetails: GWines.GET_SELECTED_WINE_INFO
            })
        },
        created () {
            this.getOrders();
            this.getPortfolio();
        },
        methods: {
            openOrder (order) {
                this.selectOrder(order);
                this.getWineDetails(order);
                this.modifyOrderDialog = true;
            },
            openCancelDialog(orderId) {
                  this.cancelOrderID = orderId;
                  this.cancelOrderDialog = true;
            },
            async cancelOrder() {
                await this.cancelOrderAsync(this.cancelOrderID).then(() => {
                    this.cancelOrderFail.visible = false;
                    this.cancelOrderSuccess.visible = true;
                });
                this.getOrders()
            },
            editOrder() {
                this.saveOrder({units: this.units, price: this.price}).then(res => {
                    if (res.succeeded) {
                        this.editOrderFail.visible = false;
                        this.editOrderSuccess.visible = true;
                        this.getOrders();
                    } else {
                        this.editOrderFail.visible = true;
                    };
                })
            },
            ...mapActions({
                getOrders: AOrders.GET_ORDERS,
                selectOrder: AOrders.SET_ACTIVE_ORDER,
                getWineDetails: AWines.GET_WINE_DETAILS,
                cancelOrderAsync: AOrders.CANCEL_ORDER,
                getPortfolio: APortfolio.GET_PORTFOLIO,
                saveOrder: AOrders.SAVE_ORDER
            })
        },
        watch: {
            selectedOrder: {
                immediate: true,
                deep: true,
                handler (v) {
                    this.price = v.price;
                    this.units = v.units;
                }
            }
        },
    }
</script>

<style scoped>
    .no-orders-info {
        margin-top: 20px;
    }
    .order-modify-btn,
    .order-cancel-btn {
        margin-top: 7px;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
        flex-direction: column;
    }
    .cancel-order-container {
        padding-top: 20px;
    }
    .cancel-order-btn {
        background: #024E7B !important;
        border-color: #024E7B!important;
        color: #FFF !important;
        width: 180px;
    }
    .sellorder-btn {
        background: #024E7B !important;
        border-color: #024E7B!important;
        color: #FFF !important;
        width: 180px;
    }
    .cancel-order-fail-text {
        color: red;
        font-size: 1em;
        text-align: center;
        width: 100%;
        display: block;
    }
    .cancel-order-success-text {
        font-size: 1em;
        text-align: center;
        width: 100%;
        display: block;
    }
    .editoffer-container {
        padding-top: 20px;
    }
    .order-country-wine {
        width: 22px;
        height: 13px;
        display: inline-block;
    }
    .vintage-label {
        margin: 0 15px;
        font-size: 16px;
    }
    .card_item_third {
        margin: 15px 0;
    }
    .card_item_third label {
        color: #024E7B;
    }
    .editorder-total-investment {
        font-size: 16px;
        text-align: right;
        font-weight: 700;
    }
</style>
<style>
    .int-pm {
        width: 100%;
    }
    .editorder-price-container {
        justify-content: flex-end;
        display: flex;
    }
</style>